  display: flex;
  flex-direction: column;
  justify-content: center;
} */

.choose-file {
  /* background-color: aqua; */
  display: flex;
  /* align-content: center !important; */
  justify-content: center !important;
  width: 100%;
  /* background-color: black; */
  /* text-align: center; */
}

.choose-file > input {
  /* background-color: red; */
  display: none;
}

.choose-file > label {
  /* background-color: red; */
  border: 1px solid black;
  cursor: pointer;
  padding: 10px;
  border-radius: 6px;
  background-color: #dedede;
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 11px 5px rgba(0,0,0,0);
}


.favrecipes {
  align-content: center;
  margin-top: 1rem, 1rem;
  background-color: salmon;
}

.favcontainer{
  flex: inherit;
  margin: 1.5em;
}

.favcontinner{

padding: 1.6em;

}

.logout-div {
  top: 15%;
  display: flex;
  justify-content: center;
  position: relative;
}
.logout-button {
  display:flex;
  position: absolute;
  font-weight: 300;
  background-color:tomato;
  border-radius: 6px;
  padding: 6px;
  /* border: 2px;
  border-style: solid 1px black; */
}

.reg-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  position: relative;
  top: 25%;
}

.reg-card {
    text-align: center;
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
    opacity: 75%;
    position: relative;
    display: inline-block;
}

.create-recipe-button {
  padding: 15px;
}

.close-button-recipe-card {
  display: flex;
  justify-content: center;
  position: relative;
}

.fav-list {
  cursor: pointer;
  text-align: center;
  font-size: 2rem;
  list-style-type: none;
