}

.title-div {
  display: flex;
  justify-content: center;
  position: relative;
}

.fav-title {
  margin-bottom: 2%;
  border-bottom: solid thin;
  padding-bottom: 5px;
  text-align: center;
  cursor: pointer;
}

.img-div {
  display:flex;
  justify-content: center;
  padding-top: 10px;
}

.fav-image {
  border-radius: 20px;
}

.ingredient-title {
  border-top: thin solid !important;
  padding-top:10px;
  text-align: center;
}

.ingredient-list {
  margin-left: 25px;
  list-style-type: disc !important;
  font-size: 1.1rem;
}

.prep-div {
  padding-top: 10px;
}

.prep-title {
  border-top: thin solid !important;
  padding-top:10px;
  text-align: center;
}

.prep-list {
  font-size: 1.1rem;
  margin-left: 10px;
}

@media screen and (max-width:612px) {
